
import './index.scss';
import { history } from '&/history';
const data = [
    {
        icon: "https://img02.hua.com/m/category/Classification/m_category_hot_Lover.png",
        text: '送恋人鲜花'
    },
    {
        icon: "https://img02.hua.com/m/category/Classification/m_category_hot_Elder.png",
        text: '送长辈鲜花'
    },
    {
        icon: "https://img02.hua.com/m/category/Classification/m_category_hot_Birthday.png",
        text: '生日鲜花'
    },
    {
        icon: "https://img02.hua.com/m/category/Classification/m_category_hot_Boxedflowers.png",
        text: '礼盒鲜花'
    },
    {
        icon: "https://img02.hua.com/m/category/Classification/m_category_hot_PPF.png",
        text: '永生花'
    },
    {
        icon: "https://img02.hua.com/m/category/Classification/m_category_hot_Cake.png",
        text: '蛋糕'
    },
    {
        icon: "https://img02.hua.com/m/category/Classification/m_category_hot_Cake_ganso.png",
        text: '元祖蛋糕'
    },
    {
        icon: "https://img02.hua.com/m/category/Classification/m_category_hot_Goldfoilflower.png",
        text: '金箔花'
    },
    {
        icon: "https://img02.hua.com/m/category/Classification/m_category_hot_Jewelry.png",
        text: '首饰'
    },
    {
        icon: "https://img02.hua.com/m/category/Classification/m_category_hot_Chocolate_qiaoluo.png",
        text: '巧克力'
    },
    {
        icon: "https://img02.hua.com/m/category/Classification/m_category_hot_CreativeGifts.png",
        text: '创意礼品'
    },
    {
        icon: "https://img02.hua.com/m/category/Classification/m_category_hot_Groupbuy.png",
        text: '企业团购'
    },
]
import { NavBar, Icon, SearchBar, Button, WhiteSpace, WingBlank, Tabs, Grid } from 'antd-mobile';
export default class Classify extends Component {
    state = {
        value: '鲜花',
        initialPage: 't2'
    };
    onChange = (value) => {
        this.setState({ value });
    };
    clear = () => {
        this.setState({ value: '' });
    };
    handleClick = () => {
        this.manualFocusInst.focus();
    }
    submit = (value) => {
        history.push(`/search?searchvalue=${value}`);
    }
    componentDidMount() {
        // console.log(this.refs.tabsdom.props.initialPage);
    }
    render() {
        const tabs = [
            { title: '1 Tab', key: 't1' },
            { title: '2 Tab', key: 't2' },
            { title: '3 Tab', key: 't3' },
        ];
        return (
            <div className="indexview">
                <div style={{ position: 'relative' }}>
                    <div style={{ width: "10%", background: '#fff' }} onClick={() => history.go(-1)}>
                        <i className="iconfont icon-zuojiantou7" style={{ fontSize: "40px" }}></i>
                    </div>
                    <SearchBar
                        style={{ width: '84%', position: 'absolute', right: 0, top: 0 }}
                        value={this.state.value}
                        placeholder="请输入关键词"
                        onSubmit={value => this.submit(value)}
                        onClear={value => console.log(value, 'onClear')}
                        onFocus={() => console.log('onFocus')}
                        onBlur={() => console.log('onBlur')}
                        onCancel={() => console.log('onCancel')}
                        showCancelButton
                        onChange={this.onChange}
                    />
                </div>
                <div style={{ height: 700 }}>
                    <WhiteSpace />
                    <Tabs
                        ref="tabsdom"
                        onChange={(index) => {
                            this.setState({
                                initialPage: index.key
                            })
                        }}
                        tabs={tabs}
                        initialPage={'t2'}
                        animated={true}
                        useOnPan={true}
                        tabBarPosition="left"
                        tabDirection="vertical"
                    >
                        {
                            [
                                (
                                    this.state.initialPage == "t1" &&
                                    <div onClick={() => history.push("/findmore")} key="t1" style={{ backgroundColor: '#fff', overflow: 'hidden' }}
                                    >
                                        {
                                            <div style={{ margin: '10px', height: '100%' }}>
                                                <img style={{ width: '100%' }} src="https://img02.hua.com/m/category/Classification/hot.png" alt="" />
                                                <div style={{ boxShadow: "3px 4px 12px #333333", borderRadius: '5px', marginTop: '10px', display: 'flex', flexWrap: "wrap" }}>
                                                    {
                                                        data.map((m, i) => {
                                                            return (
                                                                <div key={i} style={{ fontSize: '12px', width: '33%', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                                                                    <img style={{ width: "50%", margin: '10px 0', borderRadius: "50%" }}
                                                                        src={
                                                                            m.icon
                                                                        } />
                                                                    <p style={{ lineHeight: '20px' }}>{m.text}</p>
                                                                </div>
                                                            )
                                                        })
                                                    }
                                                </div>
                                            </div>
                                        }
                                    </div>
                                ),
                                (
                                    this.state.initialPage == "t2" &&
                                    <div onClick={() => history.push("/findmore")} key="t2" style={{ backgroundColor: '#fff', overflow: 'hidden' }}
                                    >
                                        {
                                            <div style={{ margin: '10px', height: '100%' }}>
                                                <img style={{ width: '100%' }} src="https://img02.hua.com/m/category/Classification/hot.png" alt="" />
                                                <div style={{ boxShadow: "3px 4px 12px #333333", borderRadius: '5px', marginTop: '10px', display: 'flex', flexWrap: "wrap" }}>
                                                    {
                                                        data.map((m, i) => {
                                                            return (
                                                                <div key={i} style={{ fontSize: '12px', width: '33%', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                                                                    <img style={{ width: "50%", margin: '10px 0', borderRadius: "50%" }}
                                                                        src={
                                                                            m.icon
                                                                        } />
                                                                    <p style={{ lineHeight: '20px' }}>{m.text}</p>
                                                                </div>
                                                            )
                                                        })
                                                    }
                                                </div>
                                            </div>
                                        }
                                    </div>
                                ),
                                (
                                    this.state.initialPage == "t3" &&
                                    <div onClick={() => history.push("/findmore")} key="t3" style={{ backgroundColor: '#fff', overflow: 'hidden' }}
                                    >
                                        {
                                            <div style={{ margin: '10px', height: '100%' }}>
                                                <img style={{ width: '100%' }} src="https://img02.hua.com/m/category/Classification/hot.png" alt="" />
                                                <div style={{ boxShadow: "3px 4px 12px #333333", borderRadius: '5px', marginTop: '10px', display: 'flex', flexWrap: "wrap" }}>
                                                    {
                                                        data.map((m, i) => {
                                                            return (
                                                                <div key={i} style={{ fontSize: '12px', width: '33%', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                                                                    <img style={{ width: "50%", margin: '10px 0', borderRadius: "50%" }}
                                                                        src={
                                                                            m.icon
                                                                        } />
                                                                    <p style={{ lineHeight: '20px' }}>{m.text}</p>
                                                                </div>
                                                            )
                                                        })
                                                    }
                                                </div>
                                            </div>
                                        }
                                    </div>
                                ),


                            ]
                        }
                    </Tabs>
                    <WhiteSpace />
                </div>
            </div>
        )
    }
}